import {checkPhone,checkPwd} from "@/common/common.js"
export default {
	data() {
		return {
			isHidePassword:true,
			phone:"",
			password:""
		};
	},
	onLoad() {
	},
	methods: {
		isHidePasswordClick(){
			this.isHidePassword=!this.isHidePassword
		},
		submit() {
			if(!checkPhone(this.phone)){
				return;
			};
			if(!checkPwd(this.password)){
				return;
			};
			/*
			* 登录逻辑
			*/
			this.$store.commit('update',['isLogin',true]);
			uni.reLaunch({
				url:'/pages/my/index'
			})
		}
	}
}
